.arp {
    padding-top: 100%;
    display: block;
    overflow: hidden;
    position: relative;
    width: 100%
}

.arp>* {
    bottom: 0;
    height: 100%;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%
}

@supports (object-fit: contain) {
    .arp>img {
        object-fit:contain
    }
}

